/* 6. Write CSS rules that cause the header and .collage div to be centered on the page */

header {
    text-align: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
}

.collage {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
   
}

@media (min-width: 800px) {
    .collage {
        display: grid;
        grid-template-columns: repeat(4, 200px);
        -ms-flex-pack: center;
            justify-content: center;
    }
}

